import $H from "../api/api.js"
import store from "../store/index.js"
class chat {
	constructor(arg) {
		this.url = arg.url
		this.isOnline = false
		this.socket = null
		this.reconnectTime = 0
		this.isOpenReconnect = true
		this.heartBeatInterval = null
		// 
		let user = uni.getStorageSync('userInfo')
		this.user = user ? user : {}
		// 
		this.TO = false;
		// 
		if (this.user.token) {
			this.connectSocket()
		}
	}
	//
	exitChat(detail){
		this.socket.send({
			data: JSON.stringify({
				userIdTo: detail.to_id, // / id
				userIdFrom: this.user.userId, // id
				text: '', // 
				date:new Date().getTime(),
				type:-10
			})
		})
	}
	// 
	reconnect() {
		if (this.isOnline) {
			return
		}
		if (this.reconnectTime >= 20) {
			return this.reconnectConfirm()
		}
		this.reconnectTime += 1
		this.connectSocket()
	}
	// socket
	connectSocket() {
		this.socket = uni.connectSocket({
			url: this.url + "/" + this.user.userId,
			complete: () => {}
		})
		// 
		this.socket.onOpen(() => this.onOpen())
		// 
		this.socket.onMessage((res) => this.onMessage(res))
		// 
		this.socket.onClose(() => this.onClose())
		// 
		this.socket.onError(() => this.onError())
	}
	// 
	onOpen() {
		this.start()
		// 
		this.isOnline = true
		// console.log('socket')
		this.isOpenReconnect = true
		// 
		this.getMessage()
	}
	// 
	getMessage() {
		$H.chatLog()
	}
	// 
	onClose() {
		console.log('?????');
		// 
		this.isOnline = false
		this.socket = null
		if (this.isOpenReconnect) {
			// this.reconnect()
			this.reconnectConfirm()
		}
		// console.log('socket')
	}
	// 
	onError() {
		console.log('?????');
		// 
		this.isOnline = false
		this.socket = null
		if (this.isOpenReconnect) {
			// this.reconnect()
			this.reconnectConfirm()
		}
		// console.log('socket')
	}
	// 
	onMessage(data) {
		let res = JSON.parse(data.data)
		console.log('',res)
		// 
		switch (res.type) {
			case -2:
				// uni.showToast({
				// 	title: res.text+'',
				// 	icon: 'none'
				// });
				break;
			case -3:
				// uni.showToast({
				// 	title: res.text+'',
				// 	icon: 'none'
				// });
				break;
			// case 'moment': // 
			// 	this.handleMoment(res.data)
			// 	break;
			case 1: //
				this.handleOnMessage(JSON.parse(res.text))
				break;
			case 2: //
				this.handleOnMessage(JSON.parse(res.text))
				break;
			case 10: //
				store.commit("setLineUp",res.text)
				break;
			case 11: //
				store.commit("setLawyerCard",res.text)
				break;
			default:
				// 
				// this.handleOnMessage(res.data)
				break;
		}
	}
	// 
	async handleOnMessage(message){
		// 
		let { data } = this.addChatDetail(message,false)
		// 
		this.updateChatList(message,false)
		// 
		uni.$emit('onMessage',message)
	}
	start(){
		let obj = {
			date:new Date(),
			text:"",
			type:-3,
			userIdTo:0
		}
		this.heartBeatInterval = setInterval(() => {
			this.socket.send({
				data: JSON.stringify(obj)
			})
		},5000)
	}
	//
	send(message,onProgress = false,chatRoomNumber){
		return new Promise(async (result,reject)=>{
			// 
			let { k } = this.addChatDetail(message)
			// 
			this.updateChatList(message)
			// 
			if(!this.checkOnline()) return reject('')
			
			// 
			let data = message.data
			// chatRoomNumber: "ChatRoom1689573771263430"
			// date: "2023-07-25T08:27:17.618Z"
			// isRead: 0
			// text: "77"
			// type: 1
			// userIdFrom: 203066
			// userIdTo: "9527"
			$H.sendMsg({
				chatRoomNumber,
				date:new Date(),
				from_id:this.user.userId,
				to_id:message.to_id || this.TO.to_id,
				userIdFrom:this.user.userId,
				userIdTo:message.to_id || this.TO.to_id,
				chat_type:message.chat_type || this.TO.chat_type, 
				type:message.type, 
				text:JSON.stringify(message),
				options:JSON.stringify(message.options)
			}).then(res=>{
				// 
				message.id = res.id
				message.sendStatus = 'success'
				message.chatRoomNumber = chatRoomNumber
				this.socket.send({
					data: JSON.stringify({
						userIdTo: message.to_id || this.TO.to_id, // / id
						userIdFrom: this.user.userId, // id
						text: message, // 
						date:new Date().getTime(),
						type:message.type
					})
				})
				if(message.type === 'video'){
					message.options = res.options
				}
				// 
				// console.log('',message);
				this.updateChatDetail(message,k)
				result(res)
			}).catch(err=>{
				// 
				message.sendStatus = 'fail'
				// 
				this.updateChatDetail(message,k)
				// 
				reject(err)
			})
		})
	}
	// 
	async addChatDetail(message,isSend = true){
		// id
		let id = message.chat_type == 1 ? (isSend ? message.to_id : message.from_id) : message.to_id
		// keychatDetail_id__/id
		let key = `chatDetail_${this.user.userId}_${message.chat_type}_${id}`
		// 
		const res = await this.getChatDetail(key)
		
		let list = [],arr = [],rows = []
		res.rows.forEach((item => {
			rows.push(JSON.parse(item.text))
		}))
		arr = rows.reverse().concat(arr)
		list = arr
		// console.log('',list)
		// 
		message.k = 'k'+list.length
		list.push(message)
		// 
		// console.log('',key)
		this.setStorage(key,list)
		// 
		return {
			data:message,
			k:message.k
		}
	}
	// 
	updateChatList(message,isSend = true){
		// 
		let list = this.getChatList()
		// 
		let isCurrentChat = false
		// / id//
		let id = 0
		let avatar = ''
		let name = ''
		console.log(33333,message);
		// 
		if(message.chat_type == 1){ // 
			// 
			isCurrentChat = this.TO ? (isSend ? this.TO.id === message.to_id : this.TO.id === message.from_id) : false
			
			id = isSend ? message.to_id : message.from_id
			avatar = isSend ? message.to_avatar : message.from_avatar
			name = isSend ? message.to_name : message.from_name
		} else { // 
			isCurrentChat = this.TO && (this.TO.id === message.to_id)
			id = message.to_id
			avatar = message.to_avatar
			name = message.to_name
		}
		
		// 
		let index = list.findIndex(item=>{
			return item.chat_type === message.chat_type && item.id === id
		})
		// 
		// let data = isSend ? message.data : `${message.from_name}: ${message.data}`
		let data = this.formatChatItemData(message,isSend)
		// 
		//  + 1
		let noreadnum = (isSend || isCurrentChat) ? 0 : 1
		if(index === -1){
			let chatItem = {
				id, // / id
				chat_type:message.chat_type, //  1 group
				avatar, // / 
				name, // / 
				update_time:(new Date()).getTime(), // 
				data, // 
				type:message.type, 		   // 
				noreadnum, // 
				istop:false, // 
				shownickname:false, // 
				nowarn:false, // 
				strongwarn:false, // 
			}
			// 
			if(message.chat_type === 'group' && message.group){
				chatItem.shownickname = true
				chatItem.name = name
				chatItem = {
					...chatItem,
					user_id:message.group.user_id, // id
					remark:"", // 
					invite_confirm:1, // 
				}
			}
			list.unshift(chatItem)
		} else { // 
			// 
			let item = list[index]
			// 
			item.update_time = (new Date()).getTime()
			item.name = name
			item.data = data
			item.type = message.type
			// 
			item.noreadnum += noreadnum
			// 
			list = this.listToFirst(list,index)
		}
		// 
		let key = `chatlist_${this.user.userId}`
		this.setStorage(key,list)
		
		// 
		// this.updateBadge(list)
		
		// vuex
		console.log(66666,list);
		uni.$emit('onUpdateChatList',list)
		return list
	}
	// 
	checkOnline(){
		if(!this.isOnline){
			// 
			this.reconnectConfirm()
			return false
		}
		return true
	}
	// 
	async updateChatDetail(message,k,isSend = true){
		// id
		let id = message.chat_type == 1 ? (isSend ? message.to_id : message.from_id) : message.to_id
		// keychatDetail_id__/id
		let key = `chatDetail_${this.user.userId}_${message.chat_type}_${id}`
		// console.log('key',key)
		// 
		const res = await this.getChatDetail(key)
		
		let list = [],arr = [],rows = []
		res.rows.forEach((item => {
			rows.push(JSON.parse(item.text))
		}))
		arr = rows.reverse().concat(arr)
		list = arr
		// console.log('',list)
		// k
		let index = list.findIndex(item=>item.k === k)
		// console.log('k',index)
		if(index === -1) return;
		list[index] = message
		// 
		this.setStorage(key,list)
	}
	// 
	reconnectConfirm(){
		this.reconnectTime = 0
		uni.showModal({
			content: '',
			confirmText:"",
			success:(res)=> {
				if (res.confirm) {
					this.connectSocket()
				}
			}
		});
	}
	// 
	getStorage(key){
		let list = uni.getStorageSync(key)
		return list ? JSON.parse(list) : []
	}
	// 
	setStorage(key,value){
		return uni.setStorageSync(key,JSON.stringify(value))
	}
	// 
	getChatDetail(key = false,chatRoom){
		// key = key ? key : `chatDetail_${this.user.userId}_${this.TO.chat_type}_${this.TO.to_id}`
		// return this.getStorage(key)
		return $H.chatLog({chatRoomNumber:chatRoom||this.TO.chatRoomNumber,pageSize:200,pageNum:1})
	}
	// 
	formatChatItemData(message,isSend){
		let data = message.data
		switch (message.type){
			case 'emoticon':
			data = '[]'
				break;
			case '2':
			data = '[]'
				break;
			case 'audio':
			data = '[]'
				break;
			case 'video':
			data = '[]'
				break;
			case 'card':
			data = '[]'
				break;
		}
		data = isSend ? data : `${message.name}: ${data}`
		return data
	}
	// 
	listToFirst(arr,index){
		if (index != 0) {
			arr.unshift(arr.splice(index,1)[0]);
		}
		return arr;
	}
	// 
	close(){
		if(this.socket){
			this.socket.close()
		}
		this.isOpenReconnect = false
		console.log('');
	}
	// 
	destoryChatObject(){
		this.TO = false
		// console.log('');
	}
	// 
	createChatObject(detail){
		this.TO = detail
		console.log('',this.TO);
	}
	// 
	getChatList(){
		let key = `chatlist_${this.user.userId}`
		return this.getStorage(key)
	}
	// 
	initChatListItem(message){
		 // 
		 let list = this.getChatList()
		 // 
		 let index = list.findIndex(item=>{
		 	return item.chat_type === message.chat_type && item.id === message.to_id
		 })
		 // 
		 let data = this.formatChatItemData(message,true)
		 // 
		 if(index === -1){
		 	let chatItem = {
		 		id:message.to_id, // / id
		 		chat_type:message.chat_type, //  user group
		 		avatar:message.to_avatar,// / 
		 		name:message.to_name,// / 
		 		update_time:(new Date()).getTime(), // 
		 		data:message.data, // 
		 		type:'system', 		   // 
		 		noreadnum:0, // 
		 		istop:false, // 
		 		shownickname:false, // 
		 		nowarn:false, // 
		 		strongwarn:false, // 
		 	}
		 	// 
		 	if(message.chat_type === 'group' && message.group){
		 		chatItem = {
		 			...chatItem,
		 			user_id:message.group.user_id, // id
		 			remark:'', // 
		 			invite_confirm:message.group.invite_confirm, // 
		 		}
		 	}
		 	list.unshift(chatItem)
		 	// 
		 	let key = `chatlist_${this.user.userId}`
		 	this.setStorage(key,list)
		 	// vuex
		 	uni.$emit('onUpdateChatList',list)
		 }
	}
	// 
	formatSendData(params){
		return {
			id:0, // id
			from_avatar:this.user.user.avatar,// 
			from_name:this.user.user.nickName || this.user.username, // 
			from_id:this.user.userId, // id
			to_id:params.to_id || this.TO.to_id, // / id
			to_name:params.to_name || this.TO.to_name, // / 
			to_avatar:params.to_avatar || this.TO.to_avatar, // / 
			chat_type:params.chat_type || this.TO.chat_type, // 
			type:params.type,// 
			data:params.data, // 
			options:params.options ? params.options : {}, // 
			create_time:(new Date()).getTime(), // 
			isremove:0, // 
			sendStatus:"success" // success,fail,pending
		}
	}
}
export default chat